import React, { Component } from "react";
import { Link } from "react-dom";
import Firebase from "./firebase";
import "./css/table.css";

let url = "https://api.exchangeratesapi.io/latest?base=";
let cur = [
    'CAD', 'HKD', 'ISK', 'PHP', 'DKK', 'HUF', 'CZK', 'GBP', 'RON', 'SEK', 'IDR',
    'INR', 'BRL', 'RUB', 'HRK', 'JPY', 'THB', 'CHF', 'EUR', 'MYR', 'BGN', 'TRY',
    'CNY', 'NOK', 'NZD', 'ZAR', 'USD', 'MXN', 'SGD', 'AUD', 'ILS', 'KRW', 'PLN'
];
let cur_name = [
    'Канадский доллар', 'Гонконгский доллар', 'Исландская крона', 'Филиппинское песо', 'Датская крона',
    'Венгерский форинт', 'Чешская крона', 'Фунт стерлингов', 'Румынский лей', 'Шведская крона', 'Индонезийская рупия',
    'Индийская рупия', 'Бразильский реал', 'Российский рубль', 'Хорватская куна', 'Японская иена', 'Тайский бат',
    'Швейцарский франк', 'Евро', 'Малайзийский ринггит', 'Болгарский лев', 'Турецкая лира', 'Китайский юань',
    'Норвежская крона', 'Новозеландский доллар', 'Южноафриканский рэнд', 'Доллар', 'Мексиканское песо',
    'Сингапурский доллар', 'Австралийский доллар', 'Израильский шекель', 'Южнокорейская вона', 'Польский злотый' 
];

class Table extends Component {
    constructor () {
        super();
        this.state = {
            data: [],
            isLoading: true,
            isLoggedIn: false,
            FirstValue: 100,
            SecondValue: 0,
            FirstCurrency: "USD",
            SecondCurrency: "RUB"
        }
    }

    componentDidMount() {
        fetch(url + "RUB")
        .then(response => response.json())
        .then(json => {
            this.setState({
                data: json.rates,
                isLoading: false
            });
        });

        Firebase.auth.onAuthStateChanged(user => {
            if (user) {
                this.setState({
                    isLoggedIn: true
                });
            } else {
                this.setState({
                    isLoggedIn: false
                });
            }
        });
    }

    calculateExchange() {
        let FirstValue = document.getElementById("currency-exchange-value-1").value;
        let FirstCurrencyHTML = document.getElementById("currency-exchange-1").value;
        let SecondCurrencyHTML = document.getElementById("currency-exchange-2").value;

        this.setState({
            FirstCurrency: FirstCurrencyHTML,
            SecondCurrency: SecondCurrencyHTML 
        }, function () {
            fetch(url + FirstCurrencyHTML)
                .then(response => response.json())
                .then(thisVariable => {
                    let calc = thisVariable.rates[SecondCurrencyHTML] * FirstValue;
                    this.setState({
                        SecondValue: calc.toFixed(2)
                    });
            });
        });
    }
    
    reverseCurrencies() {
        let first = this.state.FirstCurrency;
        let second = this.state.SecondCurrency;

        this.setState({
            FirstCurrency: second,
            SecondCurrency: first,
        }, this.calculateExchange());
    }

    renderConverter() {
        const { isLoggedIn } = this.state;
        let converterSelect = [];
        let banOverlay = [];
        
        for (let i = 0; i < cur.length; i++) {
            converterSelect.push(
                <option key={'converterCurrencies_' + cur[i]} value={cur[i]}>{cur[i]}</option>
            );
        }
        
        banOverlay.push(
            <div key="ban-overlay-div" className="ban-overlay">
                <i key="ban-logo-fas" id="ban-logo" className="fas fa-ban" style={{fontSize: 70 + 'px', color: 'white'}}></i>
            </div>
        );
        
        return (
            <div onClick={ e => !this.state.isLoggedIn ? window.location.assign("#registration") : 0} className="converterContainer">
                <div className="converter">
                    {isLoggedIn ? null : banOverlay}
                    <div className="converter-side">
                        <div className="converter-side-input">
                            <input defaultValue={this.state.FirstValue} id="currency-exchange-value-1"/>
                            <select defaultValue={this.state.FirstCurrency} id="currency-exchange-1">
                                {converterSelect}
                            </select>
                        </div>
                    </div>
                    <i onClick={ (e) => {this.reverseCurrencies()} } className="fas fa-exchange-alt"/>
                    <div className="converter-side">
                        <div className="converter-side-input">
                            <input readOnly value={this.state.SecondValue} id="currency-exchange-value-2" />
                            <select defaultValue={this.state.SecondCurrency} id="currency-exchange-2">
                                {converterSelect}
                            </select>
                        </div>
                    </div>
                    <button onClick={ (e) => this.calculateExchange()}>Convert</button>
                </div>
            </div>
        )
    }

    render() {
        const { isLoading } = this.state;

        if (isLoading) {
            return <h1>Loading...</h1>
        }

        return (
            <>
                {this.renderConverter()}
                <div className="Table">
                    <table>
                        <tbody>
                            <tr>
                                <th className="TableHeader">Код валюты</th>
                                <th className="TableHeader">Полное название валюты</th>
                                <th className="TableHeader">Курс валюты по отношению к рублю</th>
                            </tr>
                            {this.getExchangeData()}
                        </tbody>
                    </table>
                </div>
            </>
        )
    }

    getExchangeData() {
        let buf = [];
        let rates = this.state.data;

        for (let i = 0; i < cur.length; i++) {
            let currencyCode = cur[i];
            let currencyName = cur_name[i];
            let currencyRate = rates[cur[i]];

            buf.push(
                <tr>
                    <th>{currencyCode}</th>
                    <th>{currencyName}</th>
                    <th>{currencyRate}</th>
                </tr>
            );
        }
        return buf;
    }
}
export default Table;