import React, { Component } from "react";
import ReactDOM from "react-dom";
import { Link, Redirect } from "react-router-dom";
import "./css/login.css";
import Firebase from "./firebase"

class Login extends Component {
    render() {
        return (
            <div className="formBlockContainer">
                <div className="formBlock">
                    <form className="formApplication" onSubmit={e => { e.preventDefault(); this.login()}}>
                        <div className="formLogin">
                            <input defaultValue="2001varvara11@gmail.com" className="InputMain" id="login" type="email" required placeholder="ivan21@mail.ru" />
                            <label className="formLoginLabel">
                                Логин (почта)
                            </label>
                        </div>
                        <div className="formPassword">
                            <input defaultValue="123456" className="InputMain" id="password" type="password"/>
                            <label className="formPasswordLabel">
                                Пароль
                            </label>
                        </div>
                        <div className="buttonPush">
                            <button className="button" type="submit">Войти</button>
                            <label className="formRegLabel">
                                <small className="textLog">Нет аккаунта? <Link style={{color: "gray", textDecoration: "none"}} to="/registration">Зарегистрируйтесь.</Link></small>
                            </label>
                        </div>
                        <span id="statusSpan"></span>
                    </form>
                </div>
            </div>
        );
    }

    renderStatus(statusString, colorSpan) {
        let statusSpan = document.getElementById("statusSpan");
        let messageSpan = <span style={{ color: colorSpan }}>{statusString}</span>;
        ReactDOM.render(messageSpan, statusSpan);
    }

    login(){
        const email = document.getElementById("login").value;
        const password = document.getElementById("password").value;

        Firebase.auth.signInWithEmailAndPassword(email, password)
            .then(result => {
                this.renderStatus("Login successful", "green");
                Firebase.auth.setPersistence(Firebase.persistence.SESSION)
                    .then(x => {
                        window.location.assign('#');
                    })
            })
            .catch(error => {
                this.renderStatus(error.message, "red");
            });
    }
}

export default Login;