import React, { Component } from "react";
import ReactDOM from "react-dom";
import { HashRouter, Route, Link, useHistory } from 'react-router-dom';

import "./index.css"
import Table from "./components/table";
import Register from "./components/register";
import Login from "./components/login";

import Firebase from "./components/firebase";
Firebase.prototype.init();

class App extends Component {
    constructor() {
        super();
        this.state = {
            isLoggedIn: false
        };
    }

    componentDidMount() {
        Firebase.auth.onAuthStateChanged(user => {
            if (user) {
                this.setState({
                    isLoggedIn: true
                });
            } else {
                this.setState({
                    isLoggedIn: false
                });
            }
          });
    }

    render() {
        return (
            <HashRouter>
                {this.renderHeader()}
                <Route path="/" component={Table} exact />
                <Route path="/auth" component={Login} />
                <Route path="/registration" component={Register} />
            </HashRouter>
        )
    }

    renderLoggedIn() {
        return <button onClick={Firebase.prototype.logout.bind(this)} className="btn-logout">Выйти</button>
    }

    renderLoggedOut() {
        return <Link to="/auth"><button className="btn-login">Войти</button></Link>;
    }

    renderHeader() {
        const { isLoggedIn } = this.state;
        return(
            <div className="headerContainer">
                <div className="header">
                    <h1>КОНВЕРТЕР ВАЛЮТ</h1>
                    <div className="userInfo">
                        <a href="#">Главная</a>
                        {isLoggedIn ? this.renderLoggedIn() : this.renderLoggedOut()}
                    </div>
                </div>
            </div>
        )
    }
}

export default App;
ReactDOM.render(<App/>, document.getElementById("root"));