import React, { Component } from "react";
import ReactDOM from "react-dom";
import "./css/register.css";
import Firebase from "./firebase"

class Register extends Component {
    render() {
        return (
            <div className="formBlockContainer">
                <div className="formBlock">
                    <div class="title">Зарегистрироваться</div>
                    <form class="formApplication" onSubmit={e => { e.preventDefault(); this.isValid()}}>
                        <div class="formName">
                            <input class="InputMain" id="name" type="text" required minLength="2" maxLength="30" placeholder="Иван" />
                            <label class="formNameLabel">
                                ИМЯ
                                <span class="inputRequired">*</span>
                            </label>
                        </div>

                        <div class="formSurname">
                            <input class="InputMain" id="surname" type="text" required minLength="2" maxLength="30" placeholder="Иванов" />
                            <label class="formSurnameLabel">
                                ФАМИЛИЯ
                            </label>
                        </div>

                        <div class="formEmail">
                            <input class="InputMain" id="email" type="email" required placeholder="ivan21@mail.ru" />
                            <label class="formEmailLabel">
                                EMAIL
                                <span class="inputRequired">*</span>
                            </label>
                        </div>

                        <div class="formPassword">
                            <input class="InputMain" id="password" type="password"/>
                            <label class="formPasswordLabel">
                                Пароль должен содержать не менее 6 символов
                                <span class="inputRequired">*</span>
                            </label>
                        </div>

                        <div class="formPassword1">
                            <input class="InputMain" id="password1" type="password"/>
                            <label class="formPassword1Label">
                                Повторите пароль, пароли должны совпадать
                                <span class="inputRequired">*</span>
                            </label>
                        </div>

                        <div class="formPhoto">
                            <label class="formPhotoLabel">
                                ПРИКРЕПИТЬ ФОТО
                            </label>
                            <input class="InputPhoto" id="photo" type="file" accept="image/*" />
                        </div>

                        <div class="buttonPush">
                            <button class="button" type="submit">Зарегистрироваться</button>
                        </div>
                    </form>
                    <div id="statusSpan" style={{marginTop: 10+'px'}}></div>
                </div>
            </div>
        );
    }

    renderStatus(statusString, colorSpan)
    {
        let statusSpan = document.getElementById("statusSpan");
        let messageSpan = <span style={{ color: colorSpan }}>{statusString}</span>;
        ReactDOM.render(messageSpan, statusSpan);
    }

    isValid() {
        const formName = document.getElementById("name");
        const formSurname = document.getElementById("surname");
        const formEmail = document.getElementById("email");
        const formPassword = document.getElementById("password");
        const formPassword1 = document.getElementById("password1");
        const formPhoto = document.getElementById("photo");
        let uploadPh = false;

        const name_pattern = new RegExp('^[A-ZА-Я]+$', 'i');
        const email_pattern = new RegExp('^([A-ZА-Я]|[0-9]|\.|-|_)+@([A-ZА-Я]|[0-9]|\.|-|_)+\.([A-ZА-Я]|[0-9]|\.)+$', 'i');

        if (!name_pattern.test(formName.value) || !name_pattern.test(formSurname.value))
        {
            alert('Имя и фамилия могут содержать только буквы.');
            return false;
        }

        if (!email_pattern.test(formEmail.value))
        {
            alert('Некорректно введён e-mail.');
            return false;
        }

        if (formPassword.value.length < 6)
        {
            alert('Пароль должен содержать более 6 символов');
            return false;
        }

        if (formPassword.value !== formPassword1.value)
        {
            alert('Пароли не совпадают.');
            return false;
        }

        if (formPhoto.files.length !== 0)
        {
            if (formPhoto.files[0].type.includes('image/') && formPhoto.files[0].size <= 10 * 1024 * 1024)
            {
                uploadPh = true;
            }
            else
            {
                alert('Файл должен быть фотографией и не может иметь размер более чем 10Мб.');
                return false;
            }
        }

        this.registration(uploadPh);
    }

    uploadUserData(uid, userData) {
        Firebase.firestore.collection("users").doc(uid).set({
            name: userData[0],
            surname: userData[1],
            avatar: userData[2].value === "" ? false : true
        })
        .then(result => {
            this.renderStatus("Регистрация была успешно завершена", "green");

            if (userData[2].value !== "") {
                let storageRef = Firebase.storage.ref();
                let metadata = { contentType: userData[2].files[0].type };
                        
                storageRef.child(uid + '.jpg').put(userData[2].files[0], metadata)
                    .then(upload => {
                        window.location.assign('#');
                    })
                    .catch(error => {
                        this.renderStatus(error.message, "red");
                });
            }
        })
        .catch(error => {
            this.renderStatus("Ошибка отправки данных: " + error, "red");
        });
    }

    registration(uploadPhoto) {
        const formName = document.getElementById("name").value;
        const formSurname = document.getElementById("surname").value;
        const formEmail = document.getElementById("email").value;
        const formPassword = document.getElementById("password").value;
        const formPhoto = document.getElementById("photo");
        const formUserData = [ formName, formSurname, formPhoto ];
        Firebase.auth.createUserWithEmailAndPassword(formEmail, formPassword)
            .then(user => {
                this.uploadUserData(Firebase.auth.currentUser.uid, formUserData);
            })
            .catch(error => {
                this.renderStatus(error.message, 'red')
            });
    }
}
export default Register;